const ButtonDelete = ({
    props,
    nik
}: {
    props?: React.ButtonHTMLAttributes<HTMLButtonElement>
    nik?: string
}) => {
    return (
        <button className="text-danger btn p-0" data-nik={nik} {...props}>
            <i className="bi bi-trash" data-feather="delete-data" />
        </button>
    )
}

export default ButtonDelete
