import PageTitle from "./Components/PageTitle"
import Table from "./Components/Table"
import ButtonDelete from "./Components/Buttons/ButtonDelete"
import { useCallback, useEffect, useState } from "react"
import ButtonPrint from "./Components/Buttons/ButtonPrint"
import ButtonAdd from "./Components/Buttons/ButtonAdd"
import { Link, useParams } from "react-router-dom"
import { destroy, get } from "../Functions/Api"
import { ConfirmDelete, PopupAlert } from "../Functions/Alerts"

const AjukanSurat = () => {
    const [ajukanSurat, setAjukanSurat] = useState<DataT[]>()
    const { type } = useParams()

    const getAjukanSurat = useCallback(async () => {
        const res = await get("surat")
        if (res.ok) setAjukanSurat(res.data)
        console.log(res)
    }, [])

    useEffect(() => {
        getAjukanSurat()
    }, [getAjukanSurat])

    const cancel = (id: number) => {
        ConfirmDelete(async () => {
            const res = await destroy(`pengajuan-surat/${id}`)
            if (res.ok) {
                getAjukanSurat()
                PopupAlert.fire({
                    icon: "success",
                    text: res.data.message
                })
            }
        })
    }

    const RowHead = (
        <tr>
            <th>No</th>
            <th>NIK</th>
            <th>Jenis Surat</th>
            <th>Status</th>
            <th colSpan={2}>ACTION</th>
        </tr>
    )

    return (
        <>
            <PageTitle title="Ajukan Surat" />
            <ButtonAdd text="Ajukan Surat" to={`/${type}/ajukan-surat/add`} />
            <Table
                title="Tabel Pengajuan Surat"
                rowThead={RowHead}
                search={{
                    url: "surat?search=:keyword",
                    dispatch: setAjukanSurat
                }}
            >
                {ajukanSurat &&
                    ajukanSurat.map(
                        ({ pemohon: nik, info: { nama }, id, status }, i) => {
                            return (
                                <tr key={i}>
                                    <td>{++i}</td>
                                    <td>{nik}</td>
                                    <td>{nama}</td>
                                    <td>
                                        {status ? (
                                            <button
                                                className="btn btn-success d-flex align-items-center gap-1 mx-auto"
                                                disabled
                                            >
                                                <i className="bi bi-check-circle" />
                                                Selesai
                                            </button>
                                        ) : (
                                            <button
                                                className="btn btn-warning d-flex align-items-center gap-1 mx-auto"
                                                disabled
                                            >
                                                <i className="bi bi-arrow-clockwise" />
                                                Proses
                                            </button>
                                        )}
                                    </td>
                                    <td className="d-flex justify-content-center align-items-center mt-3 gap-1">
                                        <Link
                                            to={`detail/${id}?jenis=${nama}`}
                                            className="text-info me-2"
                                        >
                                            <i
                                                className="bi bi-eye"
                                                data-feather="detail-data"
                                            />
                                        </Link>
                                        <ButtonPrint
                                            to={`/ajukan-surat/cetak/${id}`}
                                        />
                                        <ButtonDelete
                                            props={{
                                                onClick: () => cancel(id)
                                            }}
                                            nik={nik}
                                        />
                                    </td>
                                </tr>
                            )
                        }
                    )}
            </Table>
        </>
    )
}

export default AjukanSurat
